html {
  box-sizing: border-box;
  min-height: 100%;
}

*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  color: #dadada;
  background: linear-gradient(217deg, #035a75cc, #0ad4b800 70.71%), linear-gradient(127deg, #052751cc, #35315300 70.71%), linear-gradient(336deg, #107f00cc, #e5083000 70.71%), radial-gradient(at 20% 80%, #00b5ff99 20px, #f3ff0048 35%, #00000038 50%), radial-gradient(at 70%, #ff009e99 20px, #ec00ca48 35%, #5a287038 50%);
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

header {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

a {
  color: #fff;
  text-decoration: none;
}

ul {
  min-width: max-content;
  margin: 1rem;
  padding: 0;
  list-style: none;
  display: flex;
}

ol {
  margin-left: 2rem;
  padding: .5rem;
  list-style-type: disc;
}

nav > ul > li {
  margin: 0 1rem;
}

footer {
  text-align: center;
  backdrop-filter: blur(5px);
  background-color: #1f1f1f80;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 2rem;
  display: flex;
}

.logo {
  aspect-ratio: 1;
  -webkit-user-select: none;
  user-select: none;
  background-color: #161616;
  border-radius: 10px;
  width: 60px;
  margin: 1rem;
  position: relative;
}

.logo > p {
  text-shadow: 1px 1px 2px green, 0 0 1em #adff2f, 0 0 .2em #00f;
  font-size: 15px;
  font-weight: bolder;
  position: absolute;
  top: .5rem;
  left: .4rem;
}

.about {
  min-height: max-content;
  margin: 2rem 0 5rem;
}

.card {
  background-color: #252525a6;
  border-radius: .2rem;
  width: 95%;
  margin: 2rem .5rem;
  padding: .4rem;
  position: relative;
}

.profile-pic {
  float: right;
  aspect-ratio: "19:9";
  border-radius: 5px;
  width: 99%;
  max-width: 150px;
  margin: .5rem;
}

.social-links {
  position: absolute;
  top: -44px;
  left: 20px;
}

.social-icon {
  background-color: #252525a6;
  border-radius: .2rem .2rem 0 0;
  width: 55px;
  height: 44px;
  padding: 10px 10px 0;
}

.social-link {
  margin-right: 1rem;
}

.title-background {
  background: linear-gradient(90deg, #fefefe 42%, #252525 -100%);
  border-radius: .2rem;
  align-self: start;
  margin: 0 5%;
}

.title {
  color: #fefefe;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #252525 42%, #fefefe -100%) text;
  padding: .5rem;
  font-size: 3rem;
  font-weight: bolder;
}

.remaining-letters {
  font-size: 1.5rem;
}

.project {
  width: 95%;
}

.project-tab {
  flex-direction: column;
  display: flex;
}

.project-title {
  max-width: fit-content;
  margin: 0;
  font-size: 1.4rem;
}

.project-tags, .project-links {
  justify-content: end;
  margin: 0;
  display: flex;
}

.fancy-btn {
  border-radius: .2rem;
  margin: .2rem;
  padding: .4rem;
}

.project-tag {
  color: #fefefe;
  background-color: #252525;
}

.project-link {
  color: #252525;
  background-color: #fefefe;
}

.contact-link {
  width: 3rem;
  transition: all .1s linear;
  transform: scale(1);
}

.contact-link:hover {
  transition: all .1s linear;
  transform: scale(1.2);
}

